/**
 * WordPress dependencies
 */
import { Button } from "@wordpress/components";
import { file } from "@wordpress/icons";
import { useMemo } from "react";
import {
	Modal as WPModal,
	Flex,
	FlexBlock,
	__experimentalScrollable as Scrollable,
} from "@wordpress/components";
import { __ } from "@wordpress/i18n";

/**
 * Internal dependencies
 */
import "./editor.scss";

const SkeletonSidebar = () => {
	return (
		<div
			className="blocklayouts-skeleton-sidebar animate-pulse"
			style={{ width: 220 }}
		>
			<div
				style={{
					height: 20,
					background: "#e0e0e0",
					borderRadius: 6,
					marginBottom: 20,
				}}
			/>
			{[...Array(8)].map((_, i) => (
				<div
					key={i}
					style={{
						height: 20,
						background: "#e0e0e0",
						borderRadius: 6,
						marginBottom: 20,
						width: `${80 + Math.random() * 20}%`,
					}}
				/>
			))}
		</div>
	);
};

const CategoryButton = ({ cat, selectedCategory, onSelect }) => (
	<Button
		icon={file}
		iconSize={20}
		key={cat.value}
		className={`blocklayouts-modal__sidebar-button ${
			selectedCategory === cat.value ? "is-selected" : ""
		}`}
		onClick={() => onSelect(cat.value)}
	>
		{cat.label}
		<span
			style={{
				flex: "1",
				textAlign: "right",
			}}
		>
			{cat.count || "0"}
		</span>
	</Button>
);

const TabButton = ({ tab, activeTab, onSelect, disabled = false }) => (
	<Button
		className={`blocklayouts-modal__tabs-button ${
			activeTab === tab.value ? "is-selected" : ""
		}`}
		onClick={() => onSelect(tab.value)}
		disabled={disabled}
		__nextHasNoMarginBottom
		__next40pxDefaultSize
	>
		{tab.label}
	</Button>
);

export const Modal = ({
	isOpen,
	onClose,
	title,
	categories = [],
	isSidebarLoading = false,
	tabs = [],
	activeTab,
	onTabChange,
	selectedCategory,
	onCategoryChange,
	headerActions = null,
	children,
	className = "blocklayouts-modal",
	overlayClassName = "blocklayouts-modal-overlay",
	isFullScreen = true,
	showCategories = true,
	showTabs = true,
	categoriesTitle = __("Categories", "blocklayouts"),
	categoryIcon = null,
}) => {
	// Convert categories to options format if needed
	const categoryOptions = useMemo(
		() =>
			categories.map((cat) => ({
				label: cat.name,
				value: cat.slug,
				count: cat.count,
			})),
		[categories],
	);

	// Convert tabs to options format if needed
	const tabOptions = useMemo(
		() =>
			tabs.map((tab) => ({
				label: tab.name || tab.label,
				value: tab.slug || tab.value,
				disabled: tab.disabled || false,
			})),
		[tabs],
	);

	if (!isOpen) return null;

	return (
		<WPModal
			overlayClassName={overlayClassName}
			className={className}
			title={title}
			onRequestClose={onClose}
			isFullScreen={isFullScreen}
			headerActions={
				<Flex gap={2}>
					{showTabs && tabOptions.length > 0 && (
						<FlexBlock>
							<div className="blocklayouts-modal__tabs">
								{tabOptions.map((tab) => (
									<TabButton
										key={tab.value}
										tab={tab}
										activeTab={activeTab}
										onSelect={onTabChange}
										disabled={tab.disabled}
									/>
								))}
							</div>
						</FlexBlock>
					)}
					{headerActions}
				</Flex>
			}
		>
			<div className="blocklayouts-modal__container">
				{showCategories && (
					<div className="blocklayouts-modal__sidebar">
						<h4
							style={{
								textTransform: "uppercase",
							}}
						>
							{categoriesTitle}
						</h4>
						{isSidebarLoading ? (
							<SkeletonSidebar />
						) : (
							<Scrollable className="blocklayouts-modal__scrollable">
								<div className="blocklayouts-modal__sidebar-buttons">
									{categoryOptions.map((cat) => (
										<CategoryButton
											key={cat.value}
											cat={cat}
											selectedCategory={selectedCategory}
											onSelect={onCategoryChange}
											icon={categoryIcon}
										/>
									))}
								</div>
							</Scrollable>
						)}
					</div>
				)}
				<div className="blocklayouts-modal__content">
					<Scrollable className="blocklayouts-modal__scrollable">
						{children}
					</Scrollable>
				</div>
			</div>
		</WPModal>
	);
};
