import { __ } from "@wordpress/i18n";
import {
	TextareaControl,
	Button,
	Flex,
	FlexItem,
	SearchControl,
	Icon,
	RangeControl,
} from "@wordpress/components";
import { useState, useEffect } from "@wordpress/element";

/**
 * Internal dependencies
 */
import { Modal } from "../../../components";
import {
	getCategories,
	icons,
	getIconByName,
	searchIcons,
	getIconType,
} from "../../../utils/icons";

/**
 * IconLibraryModal Component
 */
export default function IconLibraryModal({
	onClose,
	onIconSelect,
	onCustomSvgInsert,
	currentIconName = "",
	currentCustomSvg = "",
	currentIconSize = 64,
	currentStrokeWidth = 1,
	modalState,
	setModalState,
}) {
	const { isOpen, activeTab } = modalState;

	const categories = getCategories();

	const [previewIconSize, setPreviewIconSize] = useState(currentIconSize);
	const [previewStrokeWidth, setPreviewStrokeWidth] =
		useState(currentStrokeWidth);
	const [tempCustomSvgCode, setTempCustomSvgCode] = useState(currentCustomSvg);

	// Icon library state
	const [searchTerm, setSearchTerm] = useState("");
	const [selectedCategory, setSelectedCategory] = useState("all");
	const [filteredIcons, setFilteredIcons] = useState(icons);

	// Define tabs
	const tabsData = [
		{ label: __("Library", "blocklayouts-icon"), value: "library" },
		{ label: __("Custom SVG", "blocklayouts-icon"), value: "custom" },
	];

	// Handle tab changes
	const handleTabChange = (tabValue) => {
		setModalState((prev) => ({
			...prev,
			activeTab: tabValue,
		}));
		setSelectedCategory("all");
	};

	// Effect to filter icons when search term or category changes
	useEffect(() => {
		if (activeTab === "library") {
			let result = searchIcons(searchTerm);

			if (selectedCategory !== "all") {
				result = result.filter((icon) =>
					icon.categories.includes(selectedCategory),
				);
			}

			setFilteredIcons(result);
		}
	}, [searchTerm, selectedCategory, activeTab]);

	// Reset state when modal closes
	useEffect(() => {
		if (!isOpen) {
			setSearchTerm("");
			setSelectedCategory("all");
		}
	}, [isOpen]);

	const handleIconSelect = (iconData) => {
		if (onIconSelect) {
			onIconSelect(iconData);
		}
		onClose();
	};

	const handleClearCustomSvg = () => {
		setTempCustomSvgCode("");
	};

	const handleInsertCustomSvg = () => {
		// Apply stroke width modifications if it's a line icon
		let finalSvgCode = tempCustomSvgCode;
		const type = getIconType(tempCustomSvgCode);

		if (type === "line" && previewStrokeWidth !== 1) {
			finalSvgCode = tempCustomSvgCode.replace(
				/stroke-width="([^"]*)"/g,
				`stroke-width="${previewStrokeWidth}"`,
			);
		}

		if (onCustomSvgInsert) {
			onCustomSvgInsert({
				customSvgCode: finalSvgCode,
				iconType: type,
				strokeWidth: type === "line" ? previewStrokeWidth : 1,
			});
		}
		onClose();
	};

	// Function to render the current icon
	const renderCurrentIcon = (
		size = "24",
		customCode = null,
		customStroke = null,
	) => {
		const svgCode = customCode !== null ? customCode : currentCustomSvg;
		const currentType = getIconType(svgCode);

		if (svgCode) {
			let finalSvgCode = svgCode;

			if (currentType === "line" && customStroke !== null) {
				finalSvgCode = svgCode.replace(
					/stroke-width="([^"]*)"/g,
					`stroke-width="${customStroke}"`,
				);
			}

			return (
				<div
					className="blocklayouts-custom-svg-container"
					dangerouslySetInnerHTML={{ __html: finalSvgCode }}
					style={{ width: `${size}px`, height: `${size}px` }}
				/>
			);
		}

		const selectedIcon = getIconByName(currentIconName);
		if (selectedIcon) {
			return <Icon icon={selectedIcon.icon} size={size} />;
		}

		return <Icon icon={icons[0].icon} size={size} />;
	};

	const renderIconLibrary = () => {
		return (
			<>
				<Flex>
					<FlexItem>
						<SearchControl
							value={searchTerm}
							onChange={setSearchTerm}
							label={__("Search icons", "blocklayouts-icon")}
							placeholder={__("Search...")}
							className="blocklayouts-modal__search"
							size="compact"
							style={{
								padding: "16px 0",
							}}
						/>
					</FlexItem>
				</Flex>

				{filteredIcons.length === 0 ? (
					<p>{__("No icons found!", "blocklayouts-icon")}</p>
				) : (
					<div className="blocklayouts-modal__icons">
						{filteredIcons.map((iconData) => (
							<Button
								key={iconData.name}
								className={`blocklayouts-modal__icons-button ${
									currentIconName === iconData.name ? "is-selected" : ""
								}`}
								onClick={() => handleIconSelect(iconData)}
							>
								<Icon icon={iconData.icon} size={32} />
								<span className="icon-title">{iconData.title}</span>
							</Button>
						))}
					</div>
				)}
			</>
		);
	};

	const renderCustomSVG = () => {
		const tempIconType = getIconType(tempCustomSvgCode);

		return (
			<div className="blocklayouts-modal__custom-svg">
				<div className="blocklayouts-modal__custom-svg-editor">
					<TextareaControl
						label={__("Custom SVG code", "blocklayouts-icon")}
						value={tempCustomSvgCode}
						onChange={setTempCustomSvgCode}
						help={__(
							"Paste your custom SVG code here. It will override the selected icon.",
							"blocklayouts-icon",
							"blocklayouts",
						)}
						rows={15}
					/>
				</div>

				<div className="blocklayouts-modal__custom-svg-preview">
					<h3>{__("Preview", "blocklayouts-icon")}</h3>
					<div className="blocklayouts-icon-preview-container">
						{tempCustomSvgCode ? (
							renderCurrentIcon(
								previewIconSize,
								tempCustomSvgCode,
								previewStrokeWidth,
							)
						) : (
							<div className="blocklayouts-empty-preview">
								{__("Enter SVG code to see preview", "blocklayouts-icon")}
							</div>
						)}
					</div>

					<div className="blocklayouts-modal__custom-svg-controls">
						<RangeControl
							label={__("Icon Size", "blocklayouts-icon")}
							value={previewIconSize}
							onChange={setPreviewIconSize}
							min={16}
							max={256}
							__next40pxDefaultSize
						/>

						{tempIconType === "line" && (
							<RangeControl
								label={__("Stroke Width", "blocklayouts-icon")}
								value={previewStrokeWidth}
								onChange={setPreviewStrokeWidth}
								min={0.5}
								max={5}
								step={0.1}
								__next40pxDefaultSize
							/>
						)}
					</div>
					<div className="blocklayouts-modal__custom-svg-buttons">
						<Flex>
							<Button
								__next40pxDefaultSize
								variant="secondary"
								onClick={handleClearCustomSvg}
								isDestructive
								style={{ flex: "1", justifyContent: "center" }}
							>
								{__("Clear", "blocklayouts-icon")}
							</Button>
							<Button
								__next40pxDefaultSize
								variant="primary"
								onClick={handleInsertCustomSvg}
								disabled={!tempCustomSvgCode}
								style={{ flex: "1", justifyContent: "center" }}
							>
								{__("Insert Custom Icon", "blocklayouts-icon")}
							</Button>
						</Flex>
					</div>
				</div>
			</div>
		);
	};

	if (!isOpen) {
		return null;
	}

	return (
		<Modal
			isOpen={isOpen}
			onClose={onClose}
			title={__("Icon Library", "blocklayouts-icon")}
			categories={categories}
			showCategories={activeTab === "library"}
			tabs={tabsData}
			activeTab={activeTab}
			onTabChange={handleTabChange}
			selectedCategory={selectedCategory}
			onCategoryChange={setSelectedCategory}
			className="blocklayouts-modal"
			overlayClassName="blocklayouts-modal__overlay"
		>
			{activeTab === "library" && renderIconLibrary()}
			{activeTab === "custom" && renderCustomSVG()}
		</Modal>
	);
}
